/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Nunito', Arial, Helvetica, sans-serif;
  background: #cecdd3;
  background-image: url('https://images.unsplash.com/photo-1553905346-3b6e399115b4?q=80&w=1470&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D'); /* Background image */
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  color: white;
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  background: rgba(0, 0, 0, 0.5);
  width: 100%; /* Ensure the App container takes full width */
  box-sizing: border-box;
  padding: 20px;
}

.navbar {
  width: 100%;
  height: 80px;
  background: linear-gradient(135deg, #161418, #1e0429);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: fixed; /* Fixed position to keep navbar at the top */
  top: 0;
  left: 0;
  z-index: 1000;
  box-sizing: border-box;
}

.navbar .links {
  display: flex;
  align-items: center;
}

.links a {
  color: white;
  text-decoration: none;
  padding: 10px 15px;
  margin: 0 10px;
  border-radius: 5px;
  transition: background 0.3s ease;
}

.links a:hover {
  background: rgba(255, 255, 255, 0.2);
}

.navbar .user {
  display: flex;
  align-items: center;
}

.user p {
  font-size: 15px;
  margin-right: 10px;
  color: white;
}

.user img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.create-post {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 20px;
  margin-top: 100px; /* Add margin to avoid overlap with fixed navbar */
}

form {
  padding: 30px 20px;
  width: 320px;
  border-radius: 7px;
  background-color: rgba(26, 22, 29, 0.9);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  color: #333;
}

form:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.1);
}

input,
textarea {
  width: calc(100% - 16px);
  padding: 8px 10px;
  margin: 8px 0;
  background-color: rgba(222, 239, 248, 0.9);
  border: 1px solid #ddd;
  border-radius: 5px;
  color: #333;
  font-size: 16px;
  transition: border-color 0.3s ease;
  box-sizing: border-box;
}

input:focus,
textarea:focus {
  border-color: #120f14;
  outline: none;
}

input[type='submit'] {
  background-color: #1d1920;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

input[type='submit']:hover {
  background-color: #1a161b;
}

/* Ensure content doesn't overflow the viewport */
* {
  box-sizing: border-box;
}
